@font-face {
  font-family: Avenir Book;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Avenir Heavy;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 300;
}

.page {
  flex-direction: column;
  gap: 20px;
  margin-top: 20px;
  display: flex;
}

.search-bar {
  box-sizing: border-box;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  align-items: center;
  gap: 17px;
  width: 100%;
  height: 70px;
  display: flex;
}

.search-bar:hover {
  border: 1px solid #e1e7ed;
  box-shadow: 0 4px 4px #00000040;
}

.search-bar__photo {
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: contain;
  min-width: 19px;
  min-height: 19px;
  margin-left: 26px;
}

.search-bar__input {
  color: #3d4e61;
  border: 0;
  width: 100%;
  padding: 0;
  font-family: Avenir Book, sans-serif;
  font-size: 16px;
}

.search-bar__input:focus {
  text-shadow: 0 4px 4px #00000040;
  outline: none;
  font-family: Avenir Heavy, sans-serif;
}

.search-bar__input::placeholder {
  color: #3d4e61;
  font-family: Avenir Book, sans-serif;
}

[data-qa="small"] {
  gap: 9px;
  height: 42px;

  & .search-bar__photo {
    min-width: 11px;
    min-height: 11px;
    margin-left: 13px;
  }

  & .search-bar__input {
    font-size: 14px;
    font-weight: 400;
  }
}
